import { createAction, props } from '@ngrx/store';

import { GetAmortizationPaymentRequestParams, PresentationAmortizationPayment } from '@backbase/data-ang/loans';

import { PaymentScheduleResponse } from '../../models/loans-response.model';
import { GetAmortizationScheduleParams } from './store.model';

// Fetch amortized schedule
export const fetchAmortizationSchedule = createAction(
  '[Loans API] fetch amortization schedule',
  props<{ params: GetAmortizationScheduleParams }>(),
);
export const fetchAmortizationScheduleSuccess = createAction(
  '[Loans API] fetch amortization schedule succeed',
  props<{ response: PaymentScheduleResponse; append: boolean }>(),
);
export const fetchAmortizationScheduleError = createAction(
  '[Loans API] fetch amortization schedule ',
  props<{ error: string }>(),
);

// Fetch amortized schedule payment by id
export const fetchAmortizationPayment = createAction(
  '[Loans API] fetch amortization schedule payment by id',
  props<{ params: GetAmortizationPaymentRequestParams }>(),
);
export const fetchAmortizationPaymentSuccess = createAction(
  '[Loans API] fetch amortization schedule payment succeed',
  props<{ amortizedPayment: PresentationAmortizationPayment }>(),
);
export const fetchAmortizationPaymentError = createAction(
  '[Loans API] fetch  amortization schedule payment failures',
  props<{ error: string }>(),
);
